<template>
  <div id="home">
    <!-- header部分 -->
    <div id="header">
      <div class="left_nav">
        <ul>
          <li class="nav_li">
            <router-link to="/content" exact>首页</router-link>
          </li>
          <li class="nav_li" id="m_m">
            <router-link to="/right">信息管理</router-link>
          </li>
          <li class="nav_li">
            <router-link to="/sourceMonitor">产生源监测</router-link>
          </li>
          <li class="nav_li">
            <router-link to="/monitorVehicle" id="v_m">车辆监控</router-link>
          </li>
        </ul>
      </div>
      <div class="right_nav">
        <ul>
          <li class="nav_li">
            <router-link to="/intakeMonitor">消纳场管理</router-link>
          </li>
          <li class="nav_li">
            <router-link to="/resourceMonitor">资源场监测</router-link>
          </li>
          <li class="nav_li">
            <router-link to="/selectElBill" id="e_b">电子联单</router-link>
          </li>
          <li class="nav_li" id="s_m">
            <router-link to="/organizationManagement">系统管理</router-link>
          </li>
        </ul>
      </div>
      <div class="user">
        <!-- 用户名下拉菜单 -->
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            {{ name }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="loginout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import bus from "../../assets/js/bus";
export default {
  data() {
    return {
      name: window.sessionStorage.usern,

      //信息首页是否激活
      isActive: false,
      //系统管理是否激活
      isSystomActive: false,
      //车辆监控是否激活
      isVehicleMonitorActive: false,
      //电子联单是否激活
      isElBillActive: false
    };
  },
  methods: {
    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command == "loginout") {
        this.$store.commit("del_token");
        this.$router.push("/Login");
      }
    }
  },
  mounted() {},
  // 监听,当路由发生变化的时候执行
  watch: {
    $route(to, from) {
      //切换大背景图片
      let bg = document.getElementById("home");
      let img = new Image();
      if (to.path.indexOf("/content") != -1) {
        //  console.log("到首页了");
        // 修复切换背景图时出现的“白色闪屏”现象
        img.src = "../../../static/img/bg.png";
        // 确定图片加载完成后再进行背景图片切换
        img.onload = function() {
          bg.style.background = `url(${img.src}) repeat-y`;
        };
      } else {
        // console.log("到了其他页面");
        img.src = "../../../static/img/bg12.png";
        img.onload = function() {
          bg.style.background = `url(${img.src}) repeat`;
        };
      }
      //如果不是信息管理首页-去除样式
      if (to.path.indexOf("/right") == -1) {
        let bg = document.getElementById("m_m");
        bg.classList.remove("isActive");
        bg.setAttribute("class", "nav_li");
      }

      //如果不是系统管理首页-去除样式
      if (to.path.indexOf("/organizationManagement") == -1) {
        let bg = document.getElementById("s_m");
        bg.classList.remove("isActive");
        bg.setAttribute("class", "nav_li");
      }

      //如果不是车辆监控首页-去除样式
      if (to.path.indexOf("/monitorVehicle") == -1) {
        let bg = document.getElementById("v_m");
        bg.classList.remove("isActive");
        bg.setAttribute("class", "nav_li");
      }

      //如果不是电子联单首页-去除样式
      if (to.path.indexOf("/selectElBill") == -1) {
        let bg = document.getElementById("e_b");
        bg.classList.remove("isActive");
        bg.setAttribute("class", "nav_li");
      }
    }
  },
  created() {
    //接收信息管理侧栏的点击时数据传送-保存首页的高亮样式
    bus.$on("isActive", msg => {
      this.isActive = msg;
      if (this.isActive) {
        let bg = document.getElementById("m_m");
        // 使用添加类名的方法
        bg.classList.add("class", "isActive");
      }
    });
    //接收系统管理侧栏的点击时数据传送-保存首页的高亮样式
    bus.$on("isSystemActive", msg => {
      this.isSystemActive = msg;
      if (this.isSystemActive) {
        let bg = document.getElementById("s_m");
        // 使用添加类名的方法
        bg.classList.add("class", "isActive");
      }
    });
    //接收车辆管理侧栏的点击时数据传送-保存首页的高亮样式
    bus.$on("isVehicleMonitorActive", msg => {
      this.isVehicleMonitorActive = msg;
      if (this.isVehicleMonitorActive) {
        let bg = document.getElementById("v_m");
        // 使用添加类名的方法
        bg.classList.add("class", "isActive");
      }
    });
    //接收电子联单侧栏的点击时数据传送-保存首页的高亮样式
    bus.$on("isElBillActive", msg => {
      this.isElBillActive = msg;
      if (this.isElBillActive) {
        let bg = document.getElementById("e_b");
        // 使用添加类名的方法
        bg.classList.add("class", "isActive");
      }
    });
  }
};
</script>

<style lang="scss" scoped>
#home {
  background: url("../../../static/img/bg.png") no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
  #header {
    background: url("../../../static/img/dingbu.png") no-repeat;
    background-position: center;
    width: 100%;
    height: 7.407vh;
    background-size: 100vw 7.407vh;
    display: flex;
    font-size: 1.5vh;
    font-weight: bold;
    .left_nav {
      height: 3.704vh;
      width: 25vw;
      margin: 0.926vh 0 0 0.469vw;
      // background-color: red;
    }
    .right_nav {
      height: 3.704vh;
      width: 25vw;
      margin: 0.926vh 0 0 48vw;
      // background-color: red;
    }
    .user {
      height: 3.704vh;
      width: 25vw;
      margin: 0.926vh 0 0 0;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #ffffff;
    }
  }
}
.nav_li {
  list-style: none;
  display: flex;
  width: 6vw;
  height: 3.704vh;
  // margin-left: 0.5vw;
  // margin-right: 0.5vw;
  justify-content: center;
  align-items: center;
  // background-color: red;
  a {
    color: #ffffff;
    font-weight: bold;
  }
}
.nav_li :hover {
  background: url("../../../static/img/anniu.png") no-repeat;
  background-position: center;
  width: 6vw;
  height: 3.704vh;
  background-size: 5vw 3.6vh;
  list-style: none;
  display: flex;
  // margin-left: 0.5vw;
  // margin-right: 0.5vw;
  justify-content: center;
  align-items: center;
  // background-color: red;
  a {
    color: #ffffff;
    font-weight: bold;
  }
}
ul {
  padding: 0;
  margin: 0;
  display: flex;
}
//路由激活时更换样式
.router-link-active {
  background: url("../../../static/img/anniu.png") no-repeat !important;
  background-position: center !important;
  width: 6vw;
  height: 3.704vh;
  background-size: 5vw 3.6vh !important;
  list-style: none;
  display: flex;
  // margin-left: 0.5vw;
  // margin-right: 0.5vw;
  justify-content: center;
  align-items: center;
  // background-color: red;
  a {
    color: #ffffff;
    font-weight: bold;
  }
}
.el-dropdown-link {
  color: #fff;
  cursor: pointer;
  font-size: 1.5vh;
  font-weight: bold;
}
.el-dropdown-menu__item {
  text-align: center;
}
.el-icon-arrow-down {
  font-size: 1.111vh;
}
.isActive {
  background: url("../../../static/img/anniu.png") no-repeat !important;
  background-position: center !important;
  width: 6vw;
  height: 3.704vh;
  background-size: 5vw 3.6vh !important;
  list-style: none;
  display: flex;
  // margin-left: 0.5vw;
  // margin-right: 0.5vw;
  justify-content: center;
  align-items: center;
  // background-color: red;
  a {
    color: #ffffff;
    font-weight: bold;
  }
}
</style>



